                  d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"></path>
          </symbol>
          <symbol id="icon-clock" viewBox="0 0 32 32">
            <title>clock</title>
            <path class="path1" fill="#605f5f"
                  d="M16 29c-7.168 0-13-5.831-13-13s5.832-13 13-13c7.168 0 13 5.832 13 13s-5.832 13-13 13zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"></path>
            <path class="path2" fill="#605f5f"
                  d="M23.958 21.837l-6.958-6.489v-6.282c0-0.827-0.673-1.5-1.5-1.5s-1.5 0.673-1.5 1.5v6.934c0 0.414 0.174 0.814 0.477 1.098l7.435 6.934c0.279 0.259 0.642 0.402 1.023 0.402 0.415 0 0.814-0.174 1.096-0.477 0.564-0.605 0.532-1.555-0.073-2.12z"></path>
          </symbol>
        </defs>
      </svg>
      <div class="container">
        <div class="cart">
          <div class="page-title-normal">
            <h2 class="page-title-h2"><span>My Cart</span></h2>
          </div>
          <div class="item-list-wrap">
            <div class="cart-item">
              <div class="cart-item-head">
                <ul>
                  <li>Items</li>
                  <li>Price</li>
                  <li>Quantity</li>
                  <li>Subtotal</li>
                  <li>Edit</li>
                </ul>
              </div>
              <ul class="cart-item-list">
                <li v-for="item in cartList">
                  <div class="cart-tab-1">
                    <div class="cart-item-check">
                      <a href="javascript:;" class="checkbox-btn item-check-btn" v-bind:class="{'check':item.checked==1}" @click="editCart('checked',item)">
                        <svg class="icon icon-ok">
                          <use xlink:href="#icon-ok"></use>
                        </svg>
                      </a>
                    </div>
                    <div class="cart-item-pic">
                      <img v-bind:src="'./../../static/img/'+item.productImage" v-bind:alt="item.productName">
                    </div>
                    <div class="cart-item-title">
                      <div class="item-name">{{item.productName}}</div>
                    </div>
                  </div>
                  <div class="cart-tab-2">
                    <div class="item-price">{{item.salePrice | currency('$')}}</div>
                  </div>
                  <div class="cart-tab-3">
                    <div class="item-quantity">
                      <div class="select-self select-self-open">
                        <div class="select-self-area">
                          <a class="input-sub" @click="editCart('minu',item)">-</a>
                          <span class="select-ipt">{{item.productNum}}</span>
                          <a class="input-add" @click="editCart('add',item)" >+</a>
                        </div>
                      </div>
                    </div>
                  </div>
                  <div class="cart-tab-4">
                    <div class="item-price-total">{{(item.productNum*item.salePrice)|currency('$')}}</div>
                  </div>
                  <div class="cart-tab-5">
                    <div class="cart-item-opration">
                      <a href="javascript:;" class="item-edit-btn" @click="delCartconfirm(item.productId)">
                        <svg class="icon icon-del">
                          <use xlink:href="#icon-del"></use>
                        </svg>
                      </a>
                    </div>
                  </div>
                </li>
              </ul>
            </div>
          </div>
          <div class="cart-foot-wrap">
            <div class="cart-foot-inner">
              <div class="cart-foot-l">
                <div class="item-all-check">
                  <a href="javascript:;"  @click="toggleCheckAll">
                  <span class="checkbox-btn item-check-btn" v-bind:class="{'check':checkAllFlag}">
                      <svg class="icon icon-ok"><use xlink:href="#icon-ok"/></svg>
                  </span>
                    <span>Select all</span>
                  </a>
                </div>
              </div>
              <div class="cart-foot-r">
                <div class="item-total">
                  Item total: <span class="total-price">{{ totalPrice | currency('$') }}</span>
                </div>
                <div class="btn-wrap">
                  <a class="btn btn--red" v-bind:class="{'btn--dis':checkedCount==0}" @click="chekout">Checkout</a>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Modal v-bind:mdShow="modalConfirm" @close="closeModal">
        <p slot="message">确定要删除这件商品吗</p>
        <div slot="btnGroup" >
          <a class="btn btn--m" href="javascript:;" @click="delCart">确认</a>
          <a class="btn btn--m" href="javascript:;" @click="modalConfirm = false">关闭</a>
        </div>
      </Modal>
      <nav-footer></nav-footer>
    </div>
</template>
<style>
  .input-sub,.input-add{
    min-width: 40px;
    height: 100%;
    border: 0;
    color: #605F5F;
    text-align: center;
    font-size: 16px;
    overflow: hidden;
    display: inline-block;
    background: #f0f0f0;
  }
  .item-quantity .select-self-area{
    background:none;
    border: 1px solid #f0f0f0;
  }
  .item-quantity .select-self-area .select-ipt{
    display: inline-block;
    padding:0 3px;
    width: 30px;
    min-width: 30px;
    text-align: center;
  }
</style>
<script>
    import './../assets/css/checkout.css'
    import Modal from './../components/Modal'
    import NavHeader from './../components/NavHeader'
    import NavFooter from './../components/NavFooter'
    import NavBread from './../components/Navbread'
    import axios from 'axios'
    import {currency} from './../assets/js/currency'
    export default{
        data(){
            return {
                msg: 'hello vue',
                cartList:[],
                modalConfirm:false,
                productId:''
//              checkAllFlag:false
            }
        },
      mounted(){
            this.init();
      },
      computed:{
        checkAllFlag(){
            return this.checkedCount == this.cartList.length;
        },
        checkedCount(){
            var i = 0;
            this.cartList.forEach((item)=>{
                if(item.checked == '1'){
                    i++;
                }

            })
          return i;
        },
        totalPrice(){
            let money = 0;
          this.cartList.forEach((item)=>{
            if(item.checked=='1'){
                money +=parseFloat(item.salePrice)*parseInt(item.productNum);
            }
          });
          return money;
        }
      },
      filters:{
          currency:currency
      },
      components:{
        NavHeader,
        NavFooter,
        NavBread,
        Modal
      },
      methods:{
            init(){
                axios.get("/users/cartList").then((response)=>{
                    let res = response.data;
                    this.cartList = res.result;
                })
            },
        closeModal(){
          this.modalConfirm = false;
        },
        delCart(){
          axios.post("/users/cartDel",{
            productId:this.productId
          }).then((response)=>{
              let res = response.data;
              console.log(this.productId)
              if (res.status=="0"){
                this.modalConfirm = false;
                this.init();
                this.$store.commit("updateCartCount",-1);
              }
          })
        },
        delCartconfirm(productId){
            this.productId = productId
            this.modalConfirm = true;
        },
        editCart(flag,item){
            if (flag=="add"){
                item.productNum++;

            }else if(flag=="minu"){
                if (item.productNum<=1) {
                    return;
                }
              item.productNum--;

            }else{
                item.checked = item.checked=='1'?'0':'1';
            }
            axios.post("/users/cartEdit",{
                productId:item.productId,
                productNum:item.productNum,
                checked:item.checked
            }).then((response)=>{
                let res = response.data;
                let num =0;
                if(flag=='add'){
                    num =1;
                }else if(flag=='minu'){
                    num=-1
                }
              this.$store.commit("updateCartCount",num);

            })
        },
        toggleCheckAll(){
            var flag = !this.checkAllFlag;
            this.cartList.forEach((item)=>{
                item.checked = flag?'1':'0';
            })
          axios.post("/users/editCheckAll",{
              checkAll:this.checkAllFlag
          }).then((response)=>{
                let res = response.data;
                if (res.status=="0"){
                    console.log("update success")
                }
          })
        },
        chekout(){
            if(this.checkedCount>0){
                this.$router.push({
                  path:"/address"
                });
            }
        }
      }
    }
</script>



